body {
    background-color: rgb(207, 195, 184);
    font-family: "Karla", serif;
}

main {
    padding: 1em;
    max-width: 1140px;
    margin: 0 auto;
}

h1 {
    font-family: "Karla", serif;
    font-size: 1.2em;
    font-style: normal;
    font-weight: 400;
    line-height: 1.5em;
    opacity: 70%;
    text-align: center;
}

.menu-toggle {
  display: none;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  background-color: #dcd7d3;
}

.menu-toggle .bars {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  height: 1.2em;
  margin-right: 0;
  padding: 0.5em;
  background-color: #dcd7d3;
}

.menu-toggle .bar {
  display: block;
  display: block;
  width: 1.5em;
  height: 0.2em;
  background-color: #000000;
  margin: 0;
}

p {
  line-height: 1.5em;
  font-size: 1.2em;
}

h2 {
    font-family: "Times New Roman", sans-serif;
    font-size: 1.5em;
    font-style: normal;
    font-weight: 400;
    line-height: 1.5;
    text-decoration: none;
    text-align: center;
}

.bar {
    width: 100%;
    height: 1px;
    background-color: white;
    margin: 1em 0;
    opacity: 70%;
    width: 70%;
    display: flex;
    align-items: center;
    margin: auto;
}

nav {
    display: flex;
    justify-content: center;
}

.menu-nav li a:hover {
    color: rgba(153, 134, 117, 1);
}

.menu-nav li a {
    text-decoration: none;
    color: black;
    font-size: 1.2em;
    text-align: center;
  }

 .menu-nav li {
    border-right: 0.5px solid black;
    padding-right: 2.5em;
  }

  .menu-nav ul {
    list-style-type: none;
    display: flex;
    gap: 5em;
  }

  h3 {
    margin-left: 3em;
    font-size: 1.3em;
    font-family: "Karla", serif;
    padding-top: 2em;
  }

  .information p {
    padding-left: 4em;
    width: 75%;
    padding-top: 2em;
    padding-bottom: 3em;
    line-height: 1.5em;
  }

  .bar-brown {
    width: 15%;
    height: 2px;
    background-color: rgba(153, 134, 117, 1);
    margin-left: 4em;
  }

  .underline {
    text-decoration: underline;
}

.footer {
    display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer-left {
    align-items: flex-start;
    display: block;
}

.footer-left p {
    padding-top: 0;
    margin-top: 0;
    font-family: "Karla", serif;
    font-size: 1em;
    font-style: normal;
    font-weight: 400;
    line-height: 1.5em;
    opacity: 70%;
}

.footer-right {
    align-items: flex-end;
    display: block;
    white-space: nowrap;
}

.footer-right p {
    padding-top: 0;
    margin-top: 0;
    font-family: "Karla", serif;
    font-size: 1em;
    font-style: normal;
    font-weight: 400;
    line-height: 1.5em;
    opacity: 70%;
}

.bar-footer {
    margin-top: 10em;
    margin-bottom: 2em;
    width: 100%;
    height: 1px;
    background-color: white;
    opacity: 70%;
}

.information {
  background-color: rgb(220, 215, 211);
  border-radius: 1em;
  padding-bottom: 3em;
  box-sizing: border-box;
  position: relative;
  width: 100%;
  max-width: 100%; 
}

.slideshow-container {
  display: flex;
  align-items: center;
}

.slideshow-container p {
  margin-right: 3em;
  line-height: 1.5em;
}

.slideshow-container img {
border-radius: 1em;
}

.slideshow-container picture {
  display: none;
  width: 100%;
  height: auto;
}

.slideshow-container picture.active {
  display: block;
  margin-left: 4em;
}

.searchSection {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 3em;
  flex-wrap: nowrap; 
}

.searchSection p {
 font-weight: bold;
 margin-right: 1em;
}

#searchInput {
  padding: 1em;
  max-width: 100%;
  width: 20%;
}

.table {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column; 
}

a {
  color: black;
}

#courseTable tr {
  text-align: left;
}

#courseTable {
  border-collapse: separate;
  border-spacing: 0 1em;
  width: 90%;
}

th {
  text-decoration: underline;
  cursor: pointer; 
}


th:last-child {
  text-decoration: none;
}

@media (max-width: 1240px) {

  .menu-toggle {
    display: flex;
  }

  .menu-toggle .bars {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
  }

  #nav-menu {
    display: none;
    flex-direction: row;
    flex-wrap: wrap;
    background-color: #dcd7d3;
    width: 85%;
    z-index: 10;
    max-width: 100%;
    box-sizing: border-box;
    border-radius: 1em;
  }

.menu-nav li a {
    text-decoration: none;
    color: black;
    font-size: 1.2em;
    white-space: nowrap;
  }

 .menu-nav li {
  align-items: center;
  display: flex;
justify-content: center;
  border-right: 0.5px solid black;
    padding-right: 1.5em;
    flex: 1 0 20%;
  }

  .menu-nav li:last-child {
border-right: none;
  }

  .menu-nav li:nth-child(3) {
    border-right: none;
  }

  .menu-nav ul {
    padding-top: 1em;
    padding-bottom: 1em;
    list-style-type: none;
    display: flex;
    gap: 3em;
  }

  #nav-menu.active {
    display: flex;
  }

  .slideshow-container {
    display: block;
  }

  .slideshow-container p {
    width: 55%;
    font-size: 1em;
    margin-left: 0;
  }

  #searchInput {
    padding: 1em;
    width: 25%;
  }
}

@media (max-width: 750px) {

  .searchSection {
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    margin-left: 0.5em;
  }
  
  .searchSection p {
   font-weight: bold;
   font-size: 1em;
  }
  
  #searchInput {
    padding: 1em;
    width: 30%;
  }

  .information .slideshow-container {
    width: 100%;
    max-width: 100%;
}

  .information .slideshow-container picture img {
    width: 70%;
    height: auto;
  }

.footer-left p {
  font-size: 0.8em;
}

.footer-right p {
  font-size: 0.8em;
}

p {
  font-size: 0.8em;
  line-height: 1.5em;
  width: 80%;
}

h3 {
  font-size: 1em;
}

.bar-brown {
  margin-left: 3em;
}

.slideshow-container p {
  width: 70%;
  line-height: 1.5em;
  font-size: 0.8em;
  margin-left: 1em;
}

.menu-nav li a {
  font-size: 1em;
  white-space: nowrap;
}

#courseTable {
  border-collapse: separate;
  border-spacing: 0 1em;
  width: 100%;
  margin-left: 0.5em;
}

.menu-nav ul {
  gap: 1.5em;
}

.menu-nav li {
  padding-right: 1em;
}


@media (max-width: 520px) {

.footer-right p {
  white-space: normal; 
}

.footer-left p {
  overflow-wrap: break-word;
}

#nav-menu {
  width: 100%;
}

.menu-nav li a {
  font-size: 1em;
}
.menu-nav li {
  border-right: none;
  align-items:flex-start;
  display: flex;
justify-content: flex-start;
    padding-right: 0;
    flex: 1 0 30%;
  }

.menu-nav ul {
  gap: 0;
}

h3 {
  margin-left: 1em;
}

.bar-brown {
  margin-left: 1em;
}

.information p {
  padding-left: 1em;
  line-height: 2em;
  width: 90%;
}

.slideshow-container p {
  margin-right: 1em;
  line-height: 2em;
  width: 85%;
}

.searchSection p {
  font-size: 0.8em;
 }

 #searchInput {
  padding: 0.5em;
  width: 40%;
  font-size: 0.8em;
}

#courseTable {
  font-size: 0.8em;
}
}
}